
import {
  LoginPage,
  DashContainer,
  DashboardPage,
  AccountsPage,
  EventsPage,
  TagsPage,
  SettingPage,
  NotFoundPage
} from '@/containers'


const routes = [{
    path: '/login',
    component: LoginPage,
    meta: { requiresNotAuth: true }
  },
  {
    path: '/',
    component: DashContainer,
    meta: { requiresAuth: true },
    children: [{
        path: 'dashboard',
        alias: '',
        component: DashboardPage,
        name: 'Dashboard',
        meta: { description: 'Thng k v bo co' }
      },
      {
        path: 'events',
        alias: '',
        component: EventsPage,
        name: 'S kin',
        meta: { description: 'Qun l s kin hng ngy' }
      },
      {
        path: 'tags',
        alias: '',
        component: TagsPage,
        name: 'Danh mc',
        meta: { description: 'Qun l cc danh mc' }
      },
      {
        path: 'accounts',
        alias: '',
        component: AccountsPage,
        name: 'Ti khon',
        meta: { description: 'Thng tin ti khon v chuyn khon' }
      },
      {
        path: 'setting',
        alias: '',
        component: SettingPage,
        name: 'Ci t',
        meta: { description: '' }
      }
    ]
  },
  {
    // not found handler
    path: '*',
    component: NotFoundPage
  }
]

export default routes


